@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #ffffff;
}



#picCaption{


  border-radius: 100px;
  background:#212529b0;
  padding: 10px;
  padding-left: 40px;
  padding-right: 40px;
  


}


.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 0px;
}

#searchBox:focus{

  border-color: #6c757d;
  box-shadow: 0 0 0 .5rem #6c757d49;

}

.carousel-inner{

  border-radius: 10px;
  box-shadow: 3px 3px 20px 5px #000000;

}

#progImg{

  filter: blur(3px);
}

#navTop{

  box-shadow: 3px 3px 20px 5px #00000041;
  border-radius: 0px 0px 10px 10px;
}

/*#4e555a*/
#secondContent{


  width: 100vw;
  border-radius: 0px 20px 20px 0px;
  margin-top: 5rem;
  background-color: #4e555a50;
  margin-right: auto;
  margin-left: 0px;
  


}

.infoTHM{


}

#certButton{

  background-color: #4e555a50;
  width: 250px; 

}

#certButton:hover{

  background-color:#46494b;

}

#displayImg{

  max-width: 100%;

}

#certImg:hover{

  opacity: 50%;

}

#contactBlock{

  display: block !important;
  width: 200px;
  margin: auto;
  margin-top: 10px;

}

#footerContent{

  width: 100vw;
  height: 200px;
  max-width: 100vw !important;
  background-color: #4e555a50;
}


#footerlink{

  display: block;

}

#colourFill{

  background-color: #4e555a50;
  border-radius: 10px 0px 0px 10px;
  margin-left: auto;
  margin-right: 0px;
  min-height: 500px;
  height: auto;
  width: auto !important;

}


#catPic{


  margin-bottom: 20px;

}

#catPicSlideShow{


  width: 100vw;
  border-radius: 0px 20px 20px 0px;
  margin-top: 5rem;
  background-color: #4e555a50;
  margin-right: auto;
  margin-left: 0px;
  height: 500px;
  


}

#loginForm{

  max-width: 50%;
  margin-left: auto;
  margin-right: auto;
}

#loginBtn{

  max-width: 40%;
  margin-left: auto;
  margin-right: auto;
}

#loginContainer{


  width: 100vw;
  border-radius: 0px 20px 20px 0px;
  margin-top: 5rem;
  background-color: #4e555a50;
  margin-right: auto;
  margin-left: 0px;
  height: 500px;
  


}

#mainFooter{

  margin: auto;
  text-align: center;
  text-decoration: none;

}

#footerlinks{

  text-decoration: none;
  
}